body {
  margin: 0;
  font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

/* Typography */

/* poppins-600 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  src: local('Poppins SemiBold'), local('Poppins-SemiBold'),
    url('../src/fonts/poppins-v13-latin-600.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../src/fonts/poppins-v13-latin-600.woff') format('woff'),
    /* Modern Browsers */ url('../src/fonts/poppins-v13-latin-600.ttf') format('truetype'); /* Safari, Android, iOS */
}

/* poppins-700 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  src: local('Poppins Bold'), local('Poppins-Bold'), url('../src/fonts/poppins-v13-latin-700.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../src/fonts/poppins-v13-latin-700.woff') format('woff'),
    /* Modern Browsers */ url('../src/fonts/poppins-v13-latin-700.ttf') format('truetype'); /* Safari, Android, iOS */
}

